#aboutMe {
  height: 95vh;
  display: flex;
  flex-direction: column;
}

.about-container {
  padding: 3rem;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-container > p {
  text-align: justify;
  font-size: medium;
  order: 2;
  width: 50%;
  padding: 0 1rem;
  padding-left: 5%;
  color: #fff;
  line-height: 2;
  -webkit-animation: aboutFade ease 3s;
          animation: aboutFade ease 3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes aboutFade {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes aboutFade {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.about-img {
  max-width: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 3px;
  -webkit-mask-image: linear-gradient(to right, #000000 70%, transparent 100%);
          mask-image: linear-gradient(to right, #000000 70%, transparent 100%);
  -webkit-animation: openImage ease 4s;
          animation: openImage ease 4s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes openImage {
  0% {
    max-height: 0;
  }
  100% {
    max-height: 500px;
  }
}

@keyframes openImage {
  0% {
    max-height: 0;
  }
  100% {
    max-height: 500px;
  }
}

.about-img > img {
  width: 300px;
}

@media screen and (max-width: 768px) {
  .about-container {
    flex-direction: column;
  }

  .about-img {
    order: 1;
    width: 300px;
    height: 300px;
    -webkit-mask-image: linear-gradient(to bottom, #000000 70%, transparent 100%);
            mask-image: linear-gradient(to bottom, #000000 70%, transparent 100%);
  }

  .about-container > p {
    order: 2;
    width: 100%;
    margin-top: 3rem;
    background-color: #ffffff28;
    border-radius: 2px;
  }
}

@media screen and (max-width: 425px) {
  .about-img {
    width: 150px;
    height: 150px;
  }

  .about-img > img {
    width: 150px;
  }

  .about-container > p {
    font-size: small;
    margin-top: 1rem;
  }
}

#contact {
  height: 95vh;
  display: flex;
  flex-direction: column;
}

.contact-container {
  padding: 3rem;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-container > form {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border-radius: 3px;
  flex: 50% 1;
  height: 100%;
  position: relative;
}

.contact-container > form > label {
  color: #814f81;
  font-size: large;
  font-weight: bold;
}

.contact-container > form > input,
textarea {
  margin: 1rem 0;
  padding: 1rem 0.5rem;
  font-size: medium;
  outline: none;
  color: #814f81;
  font-weight: 700;
  letter-spacing: 1px;
}

.contact-container > form > textarea {
  height: 300px;
}

.contact-container > form > input[type="submit"] {
  background-color: #74025b;
  border: 1px solid #fff;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  transition: 0.25s ease-in-out;
}

.contact-container > form > input[type="submit"]:hover {
  background-color: #814f81;
}

.handles {
  flex: 50% 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  height: 100%;
  margin-left: 1rem;
  border-radius: 3px;
}

.handles > a {
  color: #74025b;
  font-size: xx-large;
  text-decoration: none;
  display: flex;
  align-items: center;
}

.handles > a > svg {
  margin-right: 0.5rem;
}

.handles > a:hover {
  color: #814f81;
}

@media screen and (max-width: 425px) {
  .contact-container {
    overflow: scroll;
    display: block;
  }

  .handles {
    margin-top: 2rem;
    margin-left: 0;
    height: 20%;
  }

  .handles > a {
    font-size: larger;
  }
}

#experiences {
  height: 95vh;
  display: flex;
  flex-direction: column;
}

.experiences-container {
  padding: 3rem;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sub-experience {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  grid-gap: 2rem;
}

.sub-experience > .works {
  background-color: #ffffff1f;
  border-radius: 3px;
}

.sub-experience > .works > h2 {
  background-color: #fff;
  color: #74025b;
  font-size: x-large;
  text-align: center;
  padding: 0.5rem;
  border-radius: 3px 3px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sub-experience > .works > h2 > svg {
  margin-right: 0.5rem;
}

.work-list {
  padding: 0.5rem;
  color: #fff;
}

.work-list > h4 {
  margin: 1.5rem 0 0.2rem 0;
  letter-spacing: 2px;
}

.work-list > p {
  background-color: #ffffff1f;
  padding: 0.5rem;
}

@media screen and (max-width: 1024px) {
  .experiences-container {
    align-items: flex-start;
    overflow: scroll;
  }
}

#interests {
  height: 95vh;
  display: flex;
  flex-direction: column;
}

.interests-container {
  padding: 3rem;
  height: 90%;
  overflow: scroll;
}

.interests-container > h4 {
  font-size: large;
  font-weight: 500;
  color: #fff;
  letter-spacing: 3px;
  padding: 0.5rem;
  border-radius: 2px;
  margin-bottom: 1rem;
  background-color: #ffffff1f;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(11rem, 1fr));
  grid-gap: 2rem;
}

.grid-container a {
  padding: 1rem;
  background-color: #ffffff1f;
  border-radius: 3px;
  color: #fff;
  text-decoration: none;
}

.grid-container a:hover {
  background-color: #74025b8a;
}

.achievement-img {
  max-height: 200px;
  width: 100%;
  overflow: hidden;
  margin-bottom: 1rem;
  -webkit-mask-image: linear-gradient(to bottom, #000000 20%, transparent 100%);
          mask-image: linear-gradient(to bottom, #000000 20%, transparent 100%);
}

.achievement-img > img {
  width: 100%;
}

.grid-container a h4 {
  letter-spacing: 2px;
}

.grid-container a p {
  font-size: small;
}

.interests-container::-webkit-scrollbar {
  width: 0;
}

#skills {
  height: 95vh;
  display: flex;
  flex-direction: column;
}

.skills-container {
  padding: 3rem;
  height: 90%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
  grid-gap: 2rem;
}

.programs {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.programs > .logo-img {
  height: 100px;
  width: 100px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  margin-bottom: 0.5rem;
}

.programs > .logo-img > img {
  height: 50px;
}

.programs > p {
  color: #fff;
  font-size: medium;
  text-align: center;
  letter-spacing: 2px;
}

@media screen and (max-width: 1024px) {
  .skills-container {
    overflow: scroll;
  }
  .skills-container {
    padding: 1rem;
    grid-gap: 1rem;
  }

  .programs > .logo-img {
    height: 60px;
    width: 60px;
  }

  .programs > .logo-img > img {
    height: 30px;
  }
}

.menu {
  display: none;
}

.navbar > ul {
  height: 5vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  list-style: none;
  padding: 10px;
  position: fixed;
  z-index: 2;
  -webkit-animation: fadeIn ease 2s;
          animation: fadeIn ease 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
    left: 150rem;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    left: 150rem;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}

.navbar > ul > li {
  padding: 0.5rem 1rem;
}

.navbar > ul > li:hover {
  background-color: #ffffff4d;
  border-radius: 2px;
}

.navbar > ul > li > a {
  font-size: small;
  text-decoration: none;
  color: white;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
}

.navbar > ul > li > a > svg {
  margin-right: 0.5rem;
}

.resume {
  border: 2px solid #74025b;
  border-radius: 2px;
}

@media screen and (max-width: 768px) {
  .menu {
    display: block;
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;
    color: #fff;
    font-size: xx-large;
    cursor: pointer;
    z-index: 10;
  }

  .menu:hover {
    color: #e2e2e2;
  }

  .navbar > ul {
    position: absolute;
    z-index: 10;
    top: 5vh;
    grid-gap: 15px;
    gap: 15px;
    display: flex;
    flex-direction: column;
    background-color: #4b1d4b;
    height: 100%;
    -webkit-animation: none;
            animation: none;
  }

  .navbar > ul > li {
    padding: 1rem 0.5rem;
    width: 100%;
    background-color: #ffffff17;
    border-radius: 3px;
  }

  .navbar > ul > li:hover {
    color: #2c394b;
  }

  .open {
    opacity: 1;
    left: 0;
    transition: 0.2s ease-in-out;
  }

  .close {
    opacity: 0;
    left: -100rem;
    transition: 0.2s ease-in-out;
  }
}

#projects {
  height: 95vh;
  display: flex;
  flex-direction: column;
}

.projects-container {
  padding: 3rem;
  height: 90%;
  display: flex;
  direction: rtl;
  overflow-y: scroll;
}

.projects-container::-webkit-scrollbar {
  width: 4px;
}

.projects-container::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px grey;
  border-radius: 10px;
}

.projects-container::-webkit-scrollbar-thumb {
  cursor: pointer;
  background-color: #74025b;
  border-radius: 10px;
}

.projects-container::-webkit-scrollbar-thumb:hover {
  background-color: #fff;
}

.list-container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 2rem;
  direction: ltr;
}

.list-container a {
  color: #fff;
  text-decoration: none;
}

.list-container a:hover {
  color: #d8b953;
}

.project-img {
  height: 200px;
  width: 100%;
  overflow: hidden;
  margin-bottom: 0;
  border-radius: 2px;
  position: relative;
}

.project-img > img {
  width: 100%;
  -webkit-mask-image: linear-gradient(to bottom, #000000 20%, transparent 100%);
          mask-image: linear-gradient(to bottom, #000000 20%, transparent 100%);
}

.project-img > .links {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 8;
}

.project-img > .links > a > svg {
  font-size: 1.8rem;
  margin: 0.5rem;
}

.hub > p {
  font-size: medium;
  font-weight: bold;
  color: #74025b;
  margin-bottom: 0.5rem;
  background-color: #fff;
  padding: 0.5rem;
  border-radius: 2px;
  letter-spacing: 1px;
}

.tools > svg {
  color: #c575b4;
  font-size: 1.5rem;
  margin-right: 1rem;
}

.tools > span {
  color: #c575b4;
  font-size: 1.2rem;
  margin-right: 1rem;
}

.container {
  height: 100vh;
  -webkit-animation: colorOpen ease 5s;
          animation: colorOpen ease 5s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes colorOpen {
  0% {
    background-color: #000000;
  }
  100% {
    background-color: #4b1d4b;
  }
}

@keyframes colorOpen {
  0% {
    background-color: #000000;
  }
  100% {
    background-color: #4b1d4b;
  }
}

.bg-img {
  position: absolute;
  object-fit: cover;
  -webkit-animation: fade ease 1s;
          animation: fade ease 1s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
    width: 70%;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    height: 70%;
  }
  100% {
    opacity: 0.3;
    width: 100%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    height: 100%;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
    width: 70%;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    height: 70%;
  }
  100% {
    opacity: 0.3;
    width: 100%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    height: 100%;
  }
}

.main {
  position: relative;
  top: 5vh;
  height: 95vh;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

.main::-webkit-scrollbar {
  width: 10px;
}

.main::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

.main::-webkit-scrollbar-thumb {
  cursor: pointer;
  background-color: #fff;
  border-radius: 10px;
}

.main::-webkit-scrollbar-thumb:hover {
  background-color: #74025b;
}

section {
  height: 95vh;
  scroll-snap-align: start;
}

section > h1 {
  padding-bottom: 5rem;
  color: #fff;
  text-align: right;
  display: flex;
  align-items: center;
  width: 90%;
  font-family: "Prompt", sans-serif;
  font-weight: normal;
  letter-spacing: 3px;
  font-size: larger;
  font-style: italic;
  -webkit-animation: navfade ease 3s;
          animation: navfade ease 3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes navfade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes navfade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

section > h1 > hr {
  height: 0;
  -webkit-animation: fadeInAnimation ease 2s;
          animation: fadeInAnimation ease 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
    width: 0%;
  }
  100% {
    opacity: 1;
    width: 80%;
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
    width: 0%;
  }
  100% {
    opacity: 1;
    width: 80%;
  }
}

@media screen and (max-width: 768px) {
  section > h1 > hr {
    width: 50%;
  }

  @-webkit-keyframes fadeInAnimation {
    0% {
      opacity: 0;
      width: 0%;
    }
    100% {
      opacity: 1;
      width: 60%;
    }
  }

  @keyframes fadeInAnimation {
    0% {
      opacity: 0;
      width: 0%;
    }
    100% {
      opacity: 1;
      width: 60%;
    }
  }
}

